$section-grey: #f3f2f2;

$color-blue: #5290EA;
$color-orange: #F28B64;

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0; }

@font-face {
    font-family: "MontserratT";
    src: url("../fonts/Montserrat-Regular.ttf");
    font-weight: normal;
    font-style: normal; }

@font-face {
    font-family: "MontserratT";
    src: url("../fonts/Montserrat-SemiBold.ttf");
    font-weight: normal;
    font-style: normal; }

html {
    font-family: "MontserratT", Arial, "Helvetica Neue", Helvetica, sans-serif; }

.container {
    width: 1140px;
    margin: 0 auto; }

.main-section {
    position: relative;
    padding-top: 16px;
    background: $section-grey;
    min-height: 800px;
    overflow: hidden;
    &__nav-container {
        height: 69px;
        display: flex; //что бы список стал на уровне с логотипом
        align-items: center; //Что б список был по центру контейнера
        justify-content: space-between; } //Что бы список отъехал от логотипа
    &__logo {
        width: 134px; }
    &__content-container {
        position: relative;
        display: flex;
        justify-content: space-between;
        flex-direction: row; }
    &__first-circle {
        position: absolute;
        right: 53px;
        top: 130px; }
    &__second-circle {
        position: absolute;
        bottom: -126px;
        right: -126px; } }

.nav-menu {
    &__list {
        display: flex;
        flex-direction: row; }
    &__list-item {
        font-size: 18px;
        font-weight: 600;
        margin-right: 65px;
        &:hover {
            transform: scale(1.03);
            transition: 0.001s; }
        &:last-child {
            margin-right: 0; } }
    a {
        text-decoration: none;
        color: #000;
        &:hover {
            // text-decoration: underline
            color: #003cff;
            transition: 0.7s; } } }

.left-wrapper {
    display: flex;
    &__img {
        padding-top: 30px;
        z-index: 3; }
    &__blue-rectangle {
        position: absolute;
        width: 565.95px;
        height: 220px;
        left: -60px;
        top: 170px;
        background: $color-blue;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        transform: rotate(-64deg); }
    &__orange-rectangle {
        position: absolute;
        width: 618.43px;
        height: 220px;
        left: -40px;
        top: 185px;
        background: $color-orange;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        transform: rotate(-50.81deg);
        z-index: 2; }
    &__white-rectangle {
        position: absolute;
        width: 608.97px;
        height: 220px;
        left: 5px;
        top: 220px;
        background: #eeeeee;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        transform: rotate(-36.15deg); } }

.right-wrapper {
    position: absolute;
    top: 150px;
    right: 76px;
    z-index: 4;
    width: 372px;
    &__icons-box {
        padding-left: 12px; }
    &__title {
        margin-bottom: -4px; }
    &__text {
        font-weight: 400;
        font-size: 16px;
        line-height: 20px;
        color: #000000;
        margin-bottom: 20px; }
    &__divider {
        margin-bottom: 6px; }
    &__buttons {
        display: inline-flex;
        align-items: center; }
    &__btn {
        margin-right: 32px;
        background: #F28B64;
        font-weight: 600;
        font-size: 18px;
        line-height: 105.19%;
        text-align: center;
        color: #FFFFFF;
        width: 200px;
        height: 60px;
        border-radius: 100px;
        cursor: pointer;
        border: none;
        box-shadow: 2px 2px 2px #000000;
        &:hover {
            transform: scale(1.02);
            background-image: linear-gradient(92deg, $color-orange 20%, #F7CE68 100%);
            box-shadow: 4px 4px 4px #000000b6;
            color: #000000;
            transition: 1.4s; } }
    &__chat-icon {
        margin-right: 12px;
        &:hover {
            transform: scale(1.02) rotate(-8deg);
            transition: 1s; } } }

.title {
    font-weight: 600;
    font-size: 72px;
    line-height: 68px;
    color: #000000; }

.divider {
    width: 65px;
    border: 1px solid #000000;
    background: #000000; }


.icons {
    display: flex;
    align-content: center;
    &-item {
        display: flex;
        justify-content: center;
        align-items: center;
        border: 1px solid $color-blue;
        height: 40px;
        width: 40px;
        border-radius: 100px;
        font-size: 21px;
        margin-right: 17px;
        text-decoration: none;
        color: black;
        &:last-child {
            margin: 0; }
        &:hover {
            color: $color-blue;
            transform: rotate(360deg) scale(1.1);
            transition: 0.7s; } } }

.btn {
    display: inline-flex; }

.msg-cnt {
    display: inline-flex;
    align-items: center; }

.circle {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 194px;
    height: 194px;
    border-radius: 150px;
    background: linear-gradient(143.92deg, rgba(237, 123, 80, 0.5) 14.85%, rgba(72, 138, 234, 0.5) 53.25%, rgba(44, 44, 44, 0.5) 85.74%);
    &__inside-circle {
        width: 130px;
        height: 130px;
        border-radius: 150px;
        background: $section-grey; }
    &_big {
        width: 282px;
        height: 282px; }
    &_inside-big {
        height: 188px;
        width: 188px; } }

.teach {
    padding: 32px 0 54px 0;
    min-height: 700px;
    &__title {
        margin-bottom: 9px; }
    &__divider {
        margin-bottom: 40px; }
    &__guitars-wrapper {
        display: inline-flex;
        margin-bottom: 26px; }
    &__img {
        // margin-top: 25px
        width: 197px;
        height: 335px; }
    &__text {
        margin: auto auto;
        font-weight: 600;
        font-size: 36px;
        line-height: 44px;
        text-align: center;
        color: #000000;
        transform: rotate(90deg); }
    &-guitars {
        margin-right: 30px;
        display: flex;
        align-items: flex-end; }
    &__container {
        position: relative; }
    &__first-circle {
        position: absolute;
        top: 325px;
        left: -70px;
        z-index: -1; }
    &__second-circle {
        position: absolute;
        top: 27px;
        right: -54px;
        z-index: -1; } }
.white_circle {
    background: #ffffff; }

.guitar__block {
    width: 360px;
    height: 360px;
    border-radius: 10px;
    background: $section-grey; }

.orange_blk {
    background: $color-orange; }

.blue_blk {
    background: $color-blue; }

.descr-icon {
    font-weight: 400;
    font-size: 18px;
    color: #000000;
    span {
        font-weight: 600;
        font-size: 48px;
        color: #000000; } }


.icons-block {
    // background: #4499445e
    height: 100px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    &__img {
        width: 80px;
        margin-right: 20px; }
    &__item {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 239px;
        height: 92px; }
    &__descr {
        // margin-right: 50px
        &:last-child {
 } } }            // margin-right: 0

.ready {
    position: relative;
    min-height: 640px;
    background: $section-grey;
    overflow: hidden;
    &__container {
        position: relative;
        padding: 46px 0 46px 167px;
        // padding-left: 167px
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between; }
    &__left {
        position: relative;
        z-index: 1; }
    &__right {
        max-width: 480px;
        & p {
            margin-bottom: 30px;
            font-weight: 400;
            font-size: 18px;
            line-height: 22px;
            color: #000000; } }
    &__second-title {
        margin-bottom: 20px; }
    &__first-circle {
        position: absolute;
        top: 166px;
        left: 106px; }
    &__second-circle {
        position: absolute;
        top: -100px;
        right: -110px; }
    &__img-circle {
        position: absolute;
        top: 249px;
        left: 3px; } }

.footer-section {
    &__container {
        // padding-left: 550px
        padding-top: 39px;
        padding-bottom: 42px;
        position: relative;
        height: 290px;
        display: flex;
        flex-direction: row;
        justify-content: flex-end; }
    &__img {
        position: absolute;
        bottom: 0px;
        left: -250px; }
    &__grey-icon {
        background: $section-grey; }
    &__icons-box {
        margin-top: 30px; } }
